import { useState } from "react";

export default function AccountDeletionForm() {
  const [phoneNumber, setPhoneNumber] = useState("");
  const [message, setMessage] = useState("");

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Simulate form submission
    setMessage("Your request has been submitted successfully.");
  };

  return (
    <div className="max-w-md mx-auto mt-12 p-6 bg-white shadow-lg rounded-lg border border-gray-200">
      <h2 className="text-2xl font-semibold text-gray-800 mb-4 text-center">Account Deletion Request</h2>
      <p className="text-gray-600 text-center mb-6">Enter your phone number to request account deletion.</p>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-gray-700 font-medium mb-2">Phone Number</label>
          <input 
            type="tel" 
            className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500" 
            placeholder="Enter your phone number" 
            value={phoneNumber} 
            onChange={(e) => setPhoneNumber(e.target.value)} 
            required 
          />
        </div>
        <button 
          type="submit" 
          className="w-full bg-red-600 text-white py-2 rounded-lg font-semibold hover:bg-red-700 transition">
          Submit Request
        </button>
      </form>
      {message && <p className="mt-4 text-green-600 text-center font-medium">{message}</p>}
    </div>
  );
}
